.index {
    :global {
        .archive-list {
            padding: 8px 0px;
            .archive-item {
                display: flex;
                line-height: 40px;

                .title {
                    font-size: 16px;
                    width: calc(100% - 100px);
                }
                .time {
                    font-size: 14px;
                    color: var(--main-secondary-text-color);
                    width: 100px;
                    text-align: right;
                }
            }
        }
    }
}