.index {
    padding: 20px;
    :global {
        .archive-content {
            padding: 8px 0px;
            .title {
                padding-top: 10px;
                font-size: 22px;
                font-weight: bold;
            }
            .time {
                font-size: 14px;
                color: var(--main-secondary-text-color);
            }
            .content {
                padding: 20px 0px;
                line-height: 30px;
            }
        }
    }
}